import {
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Typography,
} from '@material-ui/core';
import { GET_PACKAGES } from 'modules/shared/utils/consts';
import React from 'react';
import { usePaginatedQuery } from 'react-query';
import { getPackages } from 'modules/packages/services/queries';

const GREAT_NUMBER = 100000;

export default function PackagesSelect({ value, onChange }) {
  const getAllPackages = () => getPackages({ limit: GREAT_NUMBER });
  const { isLoading, error, resolvedData: result } = usePaginatedQuery(
    [GET_PACKAGES, 1],
    getAllPackages,
  );

  if (isLoading) {
    return null;
  }

  if (error) {
    return (
      <Typography variant="body2">
        Não foi possível carregar os pacotes.
      </Typography>
    );
  }

  const packages = result.data.data.map(({ id, name }) => ({
    value: id,
    label: name,
  }));

  return (
    <FormControl variant="outlined" className="w-100">
      <InputLabel id="package-select-label">Pacote</InputLabel>
      <Select
        labelId="package-select-label"
        id="package-select"
        onChange={onChange}
        value={value}
        name="packageId"
      >
        {packages.map(({ value: pckgValue, label }) => (
          <MenuItem key={pckgValue} id={pckgValue} value={pckgValue}>
            {label}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
}
