import React from 'react';
import Container from '@material-ui/core/Container';
import { useParams } from 'react-router-dom';
import Loading from 'modules/shared/components/Loading';
import {
  GET_PACKAGE_BY_ID,
  GET_PACKAGE_POSTS_BY_ID,
} from 'modules/shared/utils/consts';
import { useQuery } from 'react-query';
import ErrorInfo from 'modules/shared/components/ErrorInfo';
import useGetCategories from 'modules/categories/hooks/useGetCategories';
import PackageFormDialog from '../components/PackageFormDialog';
import { getPackageById, getPackagePostsById } from '../services/queries';
import usePackageUpdate from '../hooks/usePackageUpdate';

const EditPackage = () => {
  const { dialog, askForSave } = usePackageUpdate();
  const params = useParams();
  const postsService = () =>
    getPackagePostsById({ id: params.id, page: 1, limit: 1000 });

  const {
    isLoading: postsLoading,
    error: postsError,
    data: postsResult,
  } = useQuery(GET_PACKAGE_POSTS_BY_ID(params.id), postsService);

  const {
    categoriesById,
    isLoading: categoriesLoading,
    error: categoriesError,
  } = useGetCategories();

  const {
    isLoading: packageLoading,
    error: packageError,
    data: packageResult,
  } = useQuery(GET_PACKAGE_BY_ID(params.id), () =>
    getPackageById({ id: params.id }),
  );

  if (packageLoading || postsLoading || categoriesLoading) {
    return <Loading />;
  }

  if (packageError || postsError || categoriesError) {
    return <ErrorInfo />;
  }

  return (
    <Container>
      <PackageFormDialog
        packageById={{
          id: packageResult.data.id,
          name: packageResult.data.name,
          description: packageResult.data.description,
        }}
        posts={postsResult.data.posts}
        categories={categoriesById}
        submit={askForSave}
      />
      {dialog}
    </Container>
  );
};

export default EditPackage;
