import React from 'react';
import {
  BrowserRouter as Router,
  Redirect,
  Route,
  Switch,
} from 'react-router-dom';

import AuthenticationRoutes from 'modules/authentication/routes/AuthenticationRoutes';
import PrivateRoute from 'modules/shared/authentication/PrivateRoute';

import PackageRoutes from 'modules/packages/routes/PackageRoutes';
import CategoriesRoutes from 'modules/categories/routes/CategoriesRoutes';
import PostPurchaseRoutes from 'modules/post-purchase/routes/PostPurchaseRoutes';
import PurchasesRoutes from 'modules/purchases/routes/PurchasesRoutes';
import GalleryRoutes from 'modules/gallery/routes/GalleryRoutes';

const AppRoutes = () => (
  <Switch>
    <Route path="/packages" component={PackageRoutes} />
    <Route path="/categories" component={CategoriesRoutes} />
    <Route path="/post-purchase" component={PostPurchaseRoutes} />
    <Route path="/purchases" component={PurchasesRoutes} />
    <Redirect from="/" to="/packages" />
  </Switch>
);

const RootRoutes = () => {
  return (
    <Router>
      <Switch>
        <Route path="/login" component={AuthenticationRoutes} />
        <Route path="/gallery" component={GalleryRoutes} />
        <PrivateRoute path="/" component={AppRoutes} />
      </Switch>
    </Router>
  );
};

export default RootRoutes;
