import React from 'react';
import { useState } from 'react';
import useNotification from 'modules/shared/hooks/useNotification';
import AlertDialog from 'modules/shared/components/AlertDialog';
import { useMutation, useQueryCache } from 'react-query';
import { publishPackage } from '../services/update';
import { GET_PACKAGES } from 'modules/shared/utils/consts';

const usePublishPackage = ({ onSuccess }) => {
  // eslint-disable-next-line no-use-before-define
  const [mutate] = useMutation((id) => publishPackage(id));
  const [dialog, setDialog] = useState(null);
  const queryCache = useQueryCache();
  const onClose = () => setDialog(null);
  const notification = useNotification();

  const askForPublishPackage = (id) => {
    const onConfirm = async () => {
      try {
        await mutate(id);
        notification.success('Pacote publicado com sucesso');
        queryCache.invalidateQueries(GET_PACKAGES);
        onClose();
        onSuccess();
      } catch (error) {
        notification.error('Ocorreu um erro ao publicar o pacote.');
      }
    };
    setDialog(
      <AlertDialog
        title="Publicar pacote"
        question="Deseja mesmo publicar este pacote?"
        open
        onConfirm={onConfirm}
        onClose={onClose}
      />,
    );
  };

  return {
    askForPublishPackage,
    dialog,
  };
};

export default usePublishPackage;
