import React from 'react';
import { useState } from 'react';
import useNotification from 'modules/shared/hooks/useNotification';
import AlertDialog from 'modules/shared/components/AlertDialog';
import { useMutation, useQueryCache } from 'react-query';
import { GET_PACKAGES } from 'modules/shared/utils/consts';
import { removePackage } from '../services/update';

const useRemovePackage = ({ onSuccess }) => {
  // eslint-disable-next-line no-use-before-define
  const [mutate] = useMutation((id) => removePackage(id));
  const queryCache = useQueryCache();
  const [dialog, setDialog] = useState(null);
  const onClose = () => setDialog(null);
  const notification = useNotification();

  const askForRemovePackage = (id) => {
    const onConfirm = async () => {
      try {
        await mutate(id);
        notification.success('Pacote removido com sucesso.');
        queryCache.invalidateQueries(GET_PACKAGES);
        onClose();
        onSuccess();
      } catch (error) {
        notification.error('Ocorreu um erro ao remover o pacote.');
      }
    };
    setDialog(
      <AlertDialog
        title="Remover pacote"
        question="Deseja mesmo remover este pacote?"
        open
        onConfirm={onConfirm}
        onClose={onClose}
      />,
    );
  };

  return {
    askForRemovePackage,
    dialog,
  };
};

export default useRemovePackage;
