import React, { useState, useEffect } from 'react';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import { CONTACT_OPTIONS, ROLE_OPTIONS } from 'modules/shared/utils/consts';
import { Container, Button, Paper, Typography, Grid, InputLabel, FormControl, Slider} from '@material-ui/core';
import Input from 'modules/shared/components/Formik/Input';
import RadioGroup from 'modules/shared/components/Formik/RadioGroup';
import useNotification from 'modules/shared/hooks/useNotification';

import { useHistory } from 'react-router-dom';
import PackagesSelect from 'modules/shared/components/PackagesSelect';
import watermark from '../../shared/utils/watermark';
import createWatermark from '../services/update';

const validationSchema = Yup.object({
  name: Yup.string().required('O campo é obrigatório.'),
  email: Yup.string()
    .email('O e-mail deve ser válido.')
    .required('O campo é obrigatório.'),
  role: Yup.string().required('O campo é obrigatório.'),
  instagram: Yup.string().required('O campo é obrigatório.'),
  phone: Yup.string().required('O campo é obrigatório.'),
});

const initialValues = {
  name: '',
  lastName: '',
  role: '0',
  contact: '0',
  instagram: '',
  phone: '',
  packageId: '',
  customName: '',
  customLastName: '',
  fontSize: 20
};

const PostPurchaseForm = () => {
  const notification = useNotification();
  const history = useHistory();
  const [img, setImage] = useState('');

  useEffect(() => {
    async function fetchWatermark() {
      const generatedWatermark = await watermark(initialValues);
      setImage(generatedWatermark);
    }
    fetchWatermark();
  }, []);

  const onSubmit = async (values) => {
    try {
      await createWatermark(values, img);
      notification.success('Venda criada com sucesso!');
      history.push('/purchases');
    } catch (_error) {
      notification.error('Ocorreu um erro ao criar uma venda.');
    }
  };

  return (
    <Container className="d-flex justify-content-center align-items-center">
      <Paper className="p-4 mt-4 w-100">
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={onSubmit}
        >
          {({ values, handleChange }) => {
            const sliderChange = async (e, fontSize) => {
              handleChange(e);
              const generatedWatermark = await watermark({
                ...values,
                fontSize: fontSize,
              });
              return setImage(generatedWatermark);
            };
            const onChange = async (e) => {
              const targetEl = e.target;
              const fieldName = targetEl.name;
              handleChange(e);
              const generatedWatermark = await watermark({
                ...values,
                [fieldName]: targetEl.value,
              });
              return setImage(generatedWatermark);
            };
            return (
              <Form>
                <Typography variant="h5" className="mb-4 text-center">
                  Nova venda
                </Typography>
                <Grid container spacing={2} className="w-100">
                  <Grid item xs={12} sm={12} md={6} lg={6}>
                    <div className="d-flex justify-content-center mt-4">
                      <img
                        src={img}
                        alt="stamp"
                        style={{
                          width: '50%',
                          minHeight: '60%',
                          borderRadius: '2px',
                        }}
                      />
                    </div>
                  </Grid>
                  <Grid item sm={12} md={6} lg={6}>
                    <Grid container spacing={2} className="mt-2">
                      <Grid item xs={12} sm={6} md={6} lg={6}>
                        <Field
                          name="role"
                          label="Cargo"
                          component={RadioGroup}
                          onChange={onChange}
                          options={ROLE_OPTIONS}
                        />
                      </Grid>
                      <Grid item xs={12} sm={6} md={6} lg={6}>
                        <Field
                          name="contact"
                          label="Contato"
                          component={RadioGroup}
                          onChange={onChange}
                          options={CONTACT_OPTIONS}
                        />
                      </Grid>
                    </Grid>
                    <Grid container spacing={2} className="mt-2">
                      <Grid item xs={12}>
                        <Field
                          name="name"
                          label="Nome"
                          component={Input}
                          onChange={onChange}
                        />
                      </Grid>
                    </Grid>
                    <Grid container spacing={2} className="mt-2">
                      <Grid item xs={12} sm={6}>
                        <Field
                          name="customName"
                          label="Primeiro Nome"
                          component={Input}
                          onChange={onChange}
                        />
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <Field
                          name="customLastName"
                          label="Ultimo Nome"
                          component={Input}
                          onChange={onChange}
                        />
                      </Grid>
                    </Grid>
                    <Grid container spacing={2} className="mt-2">
                      <Grid item xs={12} sm={6} md={6} lg={6}>
                        <Field
                          name="email"
                          label="E-mail"
                          component={Input}
                          onChange={onChange}
                        />
                      </Grid>
                      <Grid item xs={12} sm={6} md={6} lg={6}>
                        <Field name="packageId" label="Pacote">
                          {({ field: { value } }) => (
                            <PackagesSelect
                              value={value}
                              onChange={handleChange}
                            />
                          )}
                        </Field>
                      </Grid>
                    </Grid>

                    <Grid container spacing={2} className="mt-2">
                      <Grid item xs={12} sm={6} md={6} lg={6}>
                        <Field
                          name="instagram"
                          label="Instagram"
                          onChange={onChange}
                          component={Input}
                        />
                      </Grid>
                      <Grid item xs={12} sm={6} md={6} lg={6}>
                        <Field
                          name="phone"
                          label="Celular"
                          onChange={onChange}
                          component={Input}
                        />
                      </Grid>
                    </Grid>
                    <Grid item xs={12}>
                      <>
                        <InputLabel id="package-select-label">
                          Tamanho da fonte
                        </InputLabel>
                        <Field name="fontSize" label="Tamanho de fonte">
                          {({ field: { value } }) => (
                              
                              <Slider
                              defaultValue={20}
                              aria-labelledby="discrete-slider"
                              valueLabelDisplay="auto"
                              step={1}
                              onChange={sliderChange}
                              marks
                              min={8}
                              max={40}
                              />
                          )}
                        </Field>
                      </>
                    </Grid>
                    <div className="d-flex justify-content-end mt-4">
                      <Button type="submit" variant="contained" color="primary">
                        Salvar
                      </Button>
                    </div>
                  </Grid>
                </Grid>
              </Form>
            );
          }}
        </Formik>
      </Paper>
    </Container>
  );
};

export default PostPurchaseForm;
