import GalleryHeader from 'modules/shared/layout/GalleryHeader';
import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Gallery from '../views/Gallery';

const GalleryRoutes = () => {
  return (
    <>
      <GalleryHeader />
      <Switch>
        <Route exact path="/gallery/:purchaseId" component={Gallery} />
        <Redirect from="/" to="/notfound" />
      </Switch>
    </>
  );
};
export default GalleryRoutes;
