import React, { useContext } from 'react';
import Typography from '@material-ui/core/Typography';
import AuthenticationContext from 'modules/shared/authentication/AuthenticationContext';
import { useHistory, Link } from 'react-router-dom';
import useNotification from 'modules/shared/hooks/useNotification';
import logo from './logo.png';
const GalleryHeader = () => {
  const notification = useNotification();
  const history = useHistory();
  const { logout } = useContext(AuthenticationContext);
  const onLogoutClick = () => {
    logout();
    history.push('/');
    notification.success('Logout feito com sucesso!');
  };
  return (
    <div style={{
      color: '#fff',
      backgroundColor: 'rgba(0,0,0, 1)'
    }} className="d-flex justify-content-between align-items-center header p-3">
      <div className="d-flex align-items-center">
        <img src={logo} />
        <Typography className="ml-2" variant="h5">
          Você está pronta para aumentar suas vendas?
        </Typography>
      </div>
    </div>
  );
};

export default GalleryHeader;
