import React from 'react';
import { useState } from 'react';
import useNotification from 'modules/shared/hooks/useNotification';
import { useMutation, useQueryCache } from 'react-query';
import { GET_CATEGORIES } from 'modules/shared/utils/consts';
import CategoryAddDialog from '../components/CategoryAddDialog';
import { newCategory } from '../services/categories';

const useAddCategory = ({ onSuccess }) => {
  // eslint-disable-next-line no-use-before-define
  const [mutate] = useMutation((newData) => newCategory(newData));
  const queryCache = useQueryCache();
  const [dialog, setDialog] = useState(null);
  const onClose = () => setDialog(null);
  const notification = useNotification();

  const onSubmit = async (newData) => {
    try {
      await mutate(newData);
      notification.success('Categoria criada com sucesso.');
      queryCache.invalidateQueries(GET_CATEGORIES);
      onSuccess();
      onClose();
    } catch (error) {
      notification.error('Ocorreu um erro ao criar a categoria.');
    }
  };

  const askForAddCategory = () => {
    setDialog(<CategoryAddDialog open onSubmit={onSubmit} onClose={onClose} />);
  };

  return {
    askForAddCategory,
    dialog,
  };
};

export default useAddCategory;
