import React from 'react';
import { SnackbarProvider } from 'notistack';

const NotificationProvider = ({ children, autoHide = 10000 }) => {
  return (
    <SnackbarProvider
      maxSnack={3}
      autoHideDuration={autoHide}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'center',
      }}
    >
      {children}
    </SnackbarProvider>
  );
};

export default NotificationProvider;
