import React, { useState } from 'react';

import useNotification from 'modules/shared/hooks/useNotification';
import { useHistory } from 'react-router-dom';
import AlertDialog from 'modules/shared/components/AlertDialog';
import { useQueryCache } from 'react-query';
import {
  getContentType,
  GET_PACKAGES,
  GET_PACKAGE_BY_ID,
  GET_PACKAGE_POSTS_BY_ID,
} from 'modules/shared/utils/consts';
import { calculateImages } from 'modules/shared/utils/img';
import { getSignedURLS } from '../services/queries';
import { savePosts } from '../services/update';
import { uploadImage } from '../services/update';
import ProgressDialog from '../components/ProgressDialog';

const usePackageUpdate = () => {
  const [dialog, setDialog] = useState(null);
  const notification = useNotification();
  const history = useHistory();
  const onClose = () => setDialog(null);
  const queryCache = useQueryCache();

  async function updatePackage({ id, name, description, images }) {
    let signedData;
    const posts = [];

    const newImages = images.filter((image) => image.isNew);
    if (newImages.length > 0) {
      try {
        const contentTypes = images.map((image) => ({
          'Content-Type': getContentType(image),
        }));
        signedData = await getSignedURLS(contentTypes);
      } catch (error) {
        console.error(error);
        onClose();
        return notification.error('Ocorreu um erro ao iniciar a criação!');
      }

      const uploadNewErrors = [];
      for (let i = 0; i < newImages.length; i += 1) {
        const signed = signedData.data.posts[i];
        try {
          setDialog(
            <ProgressDialog
              progress={i}
              total={newImages.length}
              onClose={onClose}
            />,
          );
          await uploadImage({ url: signed.uploadURL, img: newImages[i].file });
          const { coordinates } = await calculateImages(newImages[i]);
          posts.push({
            id: signed.id,
            package_id: id,
            category_id: newImages[i].category.id,
            watermark_position: newImages[i].position.value,
            name: newImages[i].name,
            aws_path: signed.aws_path,
            content_type: getContentType(newImages[i].file),
            is_customizable: true,
            coordinate_x: coordinates && coordinates.coordinate_x,
            coordinate_y: coordinates && coordinates.coordinate_y,
          });
        } catch (error) {
          console.log(error);
          uploadNewErrors.push(newImages[i].file);
        }
      }
    }

    const updatedImages = images.filter((image) => image.isUpdated);
    for (let i = 0; i < updatedImages.length; i += 1) {
      const image = updatedImages[i];
      const { coordinates } = await calculateImages(updatedImages[i]);
      posts.push({
        id: image.id,
        package_id: id,
        category_id: image.category.id,
        watermark_position: images[i].position.value,
        name: image.name,
        aws_path: image.aws_path,
        content_type: getContentType(images[i]),
        is_customizable: true,
        coordinate_x: coordinates && coordinates.coordinate_x,
        coordinate_y: coordinates && coordinates.coordinate_y,
      });
    }

    const removedImages = images.filter((image) => image.isRemoved);
    for (let i = 0; i < removedImages.length; i += 1) {
      const image = removedImages[i];
      posts.push({
        id: image.id,
        package_id: id,
        category_id: image.category.id,
        name: image.name,
        aws_path: image.aws_path,
        is_removed: 1,
        content_type: getContentType(images[i]),
        is_customizable: true,
        coordinate_x: '0',
        coordinate_y: '0',
        watermark_position: 0,
      });
    }

    try {
      await savePosts({ id, name, description, posts });
    } catch (error) {
      onClose();
      return notification.error('Ocorreu um erro ao finalizar a criação!');
    }

    queryCache.invalidateQueries(GET_PACKAGES);
    queryCache.invalidateQueries(GET_PACKAGE_BY_ID(id));
    queryCache.invalidateQueries(GET_PACKAGE_POSTS_BY_ID(id));
    onClose();

    notification.success('Pacote adicionado com sucesso!');
    return history.push('/packages');
  }

  const askForSave = async (data) => {
    const onConfirm = () => updatePackage(data);
    setDialog(
      <AlertDialog
        title="Atualizar pacote"
        question="Você confirma essas alterações?"
        open
        onClose={onClose}
        onConfirm={onConfirm}
      />,
    );
  };

  return {
    askForSave,
    dialog,
  };
};

export default usePackageUpdate;
