import React from 'react';
import {
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Typography,
} from '@material-ui/core';
import useGetCategories from 'modules/categories/hooks/useGetCategories';

export default function CategoriesSelect({ category, onChange }) {
  const { categories, isLoading, error } = useGetCategories();

  if (isLoading) {
    return null;
  }

  if (error) {
    return (
      <Typography variant="body2">
        Não foi possível carregar as categorias.
      </Typography>
    );
  }

  return (
    <FormControl variant="outlined" className="w-100">
      <InputLabel id="category-select-label">Categoria</InputLabel>
      <Select
        labelId="category-select-label"
        id="category-select"
        value={category}
        onChange={onChange}
      >
        {Object.values(categories).map(({ id, name }) => (
          <MenuItem key={id} value={id}>
            {name}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
}
