import React from 'react';
import {
  FormControl,
  FormControlLabel,
  FormHelperText,
  FormLabel,
  Radio,
  RadioGroup as MuiRadioGroup,
} from '@material-ui/core';

export default function RadioGroup({
  field,
  form: { touched, errors },
  options,
  label,
  className,
}) {
  return (
    <div className="mt-3">
      <FormControl
        component="fieldset"
        error={Boolean(touched[field.name] && errors[field.name])}
      >
        <FormLabel component="legend">{label}</FormLabel>
        <MuiRadioGroup row {...field} className={`w-100 ${className || ''}`}>
          {options.map((option) => (
            <FormControlLabel
              key={option.value}
              value={option.value}
              control={<Radio />}
              label={option.label}
            />
          ))}
          <FormHelperText>
            {touched[field.name] && errors[field.name]}
          </FormHelperText>
        </MuiRadioGroup>
      </FormControl>
    </div>
  );
}
