import React, { useState, useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import useNotification from 'modules/shared/hooks/useNotification';
import AuthenticationContext from 'modules/shared/authentication/AuthenticationContext';
import { useHistory, Redirect } from 'react-router-dom';

const useStyles = makeStyles({
  root: {
    minWidth: 275,
  },
  logo: {
    width: 100,
    height: 100,
    borderRadius: '50%',
  },
});

const Login = () => {
  const classes = useStyles();
  const { authSession, login } = useContext(AuthenticationContext);
  const notification = useNotification();
  const [loading, setLoading] = useState(false);
  const [form, setForm] = useState({ email: '', password: '' });
  const history = useHistory();

  const onChangeEmail = (e) => {
    const { value: email } = e.target;
    setForm((prev) => ({ ...prev, email }));
  };
  const onChangePassword = (e) => {
    const { value: password } = e.target;
    setForm((prev) => ({ ...prev, password }));
  };

  const onLoginClick = async (e) => {
    e.preventDefault();
    try {
      setLoading(true);
      await login(form);
      history.push('/packages');
      notification.success('Logado com sucesso!');
    } catch (error) {
      notification.error('Erro ao fazer o login.');
    } finally {
      setLoading(false);
    }
  };

  // check leak here
  if (authSession()) {
    return <Redirect to="/packages" />;
  }

  return (
    <form
      className="d-flex justify-content-center min-h-100 align-items-center "
      noValidate
      onSubmit={onLoginClick}
      autoComplete="off"
    >
      <Card
        className={`${classes.root} d-flex flex-column justify-content-center align-items-center `}
      >
        <Typography className="mt-4" variant="h4">
          Gerenciador
        </Typography>
        <Typography className="mb-4" variant="h4">
          de Imagem
        </Typography>
        <TextField onChange={onChangeEmail} label="E-mail" variant="outlined" />
        <TextField
          onChange={onChangePassword}
          label="Senha"
          className="mt-3"
          type="password"
          variant="outlined"
        />
        <Button
          disabled={loading}
          type="submit"
          color="primary"
          className="my-4"
          variant="contained"
        >
          Entrar
        </Button>
      </Card>
    </form>
  );
};

export default Login;
