import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import PurchasesList from '../view/PurchasesList';
import PurchasesNew from '../view/PurchaseNew';

const Purchases = () => {
  return (
    <Switch>
      <Route exact path="/purchases" component={PurchasesList} />
      <Route exact path="/purchases/new" component={PurchasesNew} />
      <Redirect from="/" to="/packages" />
    </Switch>
  );
};
export default Purchases;
