import React from 'react';
import { useState } from 'react';
import useNotification from 'modules/shared/hooks/useNotification';
import { useMutation, useQueryCache } from 'react-query';
import { GET_CATEGORIES } from 'modules/shared/utils/consts';
import CategoryEditDialog from '../components/CategoryEditDialog';
import { updateCategory } from '../services/categories';

const useEditCategory = ({ onSuccess }) => {
  // eslint-disable-next-line no-use-before-define
  const [mutate] = useMutation((newData) => updateCategory(newData));
  const queryCache = useQueryCache();
  const [dialog, setDialog] = useState(null);
  const onClose = () => setDialog(null);
  const notification = useNotification();

  const onSubmit = async (newData) => {
    try {
      await mutate(newData);
      notification.success('Categoria editada com sucesso.');
      queryCache.invalidateQueries(GET_CATEGORIES);
      onSuccess();
      onClose();
    } catch (error) {
      notification.error('Ocorreu um erro ao editar a categoria.');
    }
  };

  const askForEditCategory = (data) => {
    setDialog(
      <CategoryEditDialog
        open
        data={data}
        onSubmit={onSubmit}
        onClose={onClose}
      />,
    );
  };

  return {
    askForEditCategory,
    dialog,
  };
};

export default useEditCategory;
