import React from 'react';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';
import { useParams } from 'react-router-dom';
import { CardContent } from '@material-ui/core';
import Anchor from 'modules/shared/components/Anchor';
import ReactPlayer from 'react-player';
import Loading from 'modules/shared/components/Loading';
import { GET_PURCHASE_BY_ID } from 'modules/shared/utils/consts';
import { useQuery } from 'react-query';
import ErrorInfo from 'modules/shared/components/ErrorInfo';
import { getPurchaseByPackageId } from '../services/images';

const Gallery = () => {
  const params = useParams();
  const {
    isLoading: postsLoading,
    error: postsError,
    data: postsResult,
  } = useQuery(GET_PURCHASE_BY_ID(params.purchaseId), () =>
    getPurchaseByPackageId({
      purchaseId: params.purchaseId,
    }),
  );

  if (!params.purchaseId) {
    return (
      <Container className="d-flex align-items-center h-500">
        <Typography variant="body1">
          Você deve fornecer uma compra válida.
        </Typography>
      </Container>
    );
  }

  if (postsLoading) {
    return <Loading />;
  }

  if (postsError) {
    return <ErrorInfo />;
  }

  const { postsByCategory } = postsResult.data;

  if (Object.values(postsByCategory).length < 1) {
    return (
      <Container className="d-flex justify-content-center align-items-center h-500">
        <Typography variant="body1">
          Nenhum post foi encontrado nesta compra.
        </Typography>
      </Container>
    );
  }

  return (
    <Container>
      {Object.values(postsByCategory).map(({ category, posts }) => (
        <Card key={category} className="h-100 mt-4 w-100">
          <CardContent>
            <Typography variant="h5" className="mb-4">
              {category.name}
            </Typography>
            <Grid className="mt-1" container>
              {posts.map(({ id, aws_path: awsPath, aws_path_thumb: thumb, ...rest }) => (
                <Grid key={id} item xs={12} sm={6} md={4} lg={2}>
                  <div className="w-100 d-flex flex-column align-items-center">
                    {console.log(posts)}
                    <Anchor url={awsPath}>
                      {
                        /mp4/.test(awsPath) ? (
                          <ReactPlayer
                            url={awsPath}
                            style={{
                              width: '100%',
                              minHeight: '100%',
                              borderRadius: '2px',
                            }}
                        />
                        ) : (
                          <img
                            src={thumb}
                            style={{
                              width: '100%',
                              minHeight: '100%',
                              borderRadius: '2px',
                            }}
                            alt="Post"
                        />
                        )
                      }
                    </Anchor>
                  </div>
                </Grid>
              ))}
            </Grid>
          </CardContent>
        </Card>
      ))}
    </Container>
  );
};

export default Gallery;
