import React, { useCallback, useState } from 'react';
import Button from '@material-ui/core/Button';
import {
  Typography,
  Grid,
  Paper,
  Select,
  FormControl,
  InputLabel,
  MenuItem,
  Checkbox,
  FormControlLabel,
} from '@material-ui/core';
import { Formik, Field, Form } from 'formik';
import * as Yup from 'yup';
import { getUrl } from 'modules/shared/utils/file';
import { Link } from 'react-router-dom';
import Input from 'modules/shared/components/Formik/Input';
import useNotification from 'modules/shared/hooks/useNotification';
import { compareStr } from 'modules/shared/utils/sort';
import { IMAGE_POSITIONS } from 'modules/shared/utils/consts';
import Thumb from './Thumb';
import ImageUploader from './ImageUploader';
import CategoriesSelect from './CategoriesSelect';

const validationSchema = Yup.object({
  id: Yup.string(),
  name: Yup.string()
    .required('O campo é obrigatório.')
    .min(3, 'O nome deve conter pelo menos 3 caracteres'),
  description: Yup.string().min(
    3,
    'A descrição deve conter pelo menos 10 caracteres',
  ),
});

const IMG_LIMIT = 100;
let NEW_ID = 0;
const getNewId = () => {
  NEW_ID += 1;
  return NEW_ID;
};

const PackageFormDialog = ({
  packageById = { name: '', description: '' },
  posts = [],
  categories,
  submit,
}) => {
  const initialFiles = posts.reduce((acum, post) => {
    return {
      ...acum,
      [post.id]: {
        id: post.id,
        name: post.name,
        url: `${post.aws_path}`,
        aws_path: post.aws_path,
        position: IMAGE_POSITIONS[post.watermark_position],
        category: categories[post.category_id],
        checked: false,
      },
    };
  }, {});
  const [files, setFiles] = useState(initialFiles);
  const [currentPosition, setPosition] = React.useState('');
  const [currentCategory, setCategory] = React.useState('');
  const notification = useNotification();

  // validate big files
  const onDrop = useCallback(
    async (acceptedFiles) => {
      if (Object.values(files).length > IMG_LIMIT - 1) {
        return notification.error(
          `Você pode inserir apenas ${IMG_LIMIT} imagens.`,
        );
      }

      const newFiles = await Promise.all(
        acceptedFiles.map(async (file) => {
          const id = getNewId();
          const newFile = { id, file };
          const url = await getUrl(file);
          return {
            ...newFile,
            url,
            name: file.name,
            position: IMAGE_POSITIONS[0],
            category: Object.values(categories)[0],
            checked: false,
            isNew: true,
          };
        }),
      );

      return setFiles((prev) => ({
        ...prev,
        ...Object.values(newFiles).reduce(
          (acum, current) => ({
            ...acum,
            [current.id]: current,
          }),
          {},
        ),
      }));
    },
    [files, notification, categories],
  );

  const removeImage = (id) => {
    // eslint-disable-next-line camelcase
    const { [id]: toRemove, ...restFiles } = files;
    setFiles({
      ...restFiles,
      [id]: {
        ...toRemove,
        isRemoved: toRemove.isNew ? undefined : true,
      },
    });
  };

  const onPositionChange = (event) => {
    setPosition(event.target.value);
  };

  const onCategoryChange = (event) => {
    setCategory(event.target.value);
  };

  const handleCheckChange = (id) => {
    // eslint-disable-next-line camelcase
    const { [id]: _file, ...restFiles } = files;
    setFiles({ ...restFiles, [id]: { ..._file, checked: !_file.checked } });
  };

  const onApplyClick = () => {
    const newFiles = Object.values(files).reduce(
      (acum, current) => ({
        ...acum,
        [current.id]: {
          ...current,
          checked: false,
          isUpdated: current.isNew
            ? undefined
            : current.isUpdated || current.checked,
          position: current.checked
            ? IMAGE_POSITIONS[currentPosition]
            : IMAGE_POSITIONS[current.position.value],
          category: current.checked
            ? categories[currentCategory]
            : categories[current.category.id],
        },
      }),
      {},
    );
    setFiles(newFiles);
  };

  const onSubmit = (values) => {
    if (
      Object.values(files).some(
        ({ position, category }) => !position || !category,
      )
    ) {
      return notification.error(
        'Todas as imagens devem possuir categoria e posição!',
      );
    }
    return submit({
      ...values,
      images: Object.values(files),
    });
  };

  const imagesAppearing = Object.values(files)
    .filter((file) => !file.isRemoved)
    .sort(compareStr);

  const allSelected = imagesAppearing.reduce((acum, current) => {
    return acum && current.checked;
  }, true);

  const onSelectClick = () => {
    const newFiles = Object.values(files).reduce(
      (acum, current) => ({
        ...acum,
        [current.id]: {
          ...current,
          checked: !allSelected,
        },
      }),
      {},
    );
    setFiles(newFiles);
  };

  return (
    <Paper className="p-4 mt-4">
      <Typography className="mb-4" variant="h5">
        Novo Pacote de Imagens
      </Typography>
      <Formik
        initialValues={packageById}
        validationSchema={validationSchema}
        onSubmit={onSubmit}
      >
        <Form>
          <Field name="name" label="Nome do pacote" component={Input} />
          <Field
            name="description"
            label="Descrição do pacote"
            className="mt-4"
            component={Input}
          />
          <Grid container alignItems="center" className="mt-4">
            <Grid item xs={12} sm={6} md={2} lg={1}>
              <Typography variant="h6">Imagens</Typography>
            </Grid>
            {imagesAppearing.length > 0 && (
              <Grid item xs={12} sm={12} md={12} lg={12}>
                <FormControlLabel
                  control={(
                    <Checkbox
                      checked={allSelected}
                      onChange={onSelectClick}
                      name="checkedB"
                      color="primary"
                    />
                  )}
                  label="Todos"
                />
              </Grid>
            )}
          </Grid>

          {imagesAppearing.some((file) => file.checked) && (
            <Grid container spacing={2} className="mt-1" alignItems="center">
              <Grid item xs={12} sm={6} md={2} lg={2}>
                <FormControl variant="outlined" className="w-100">
                  <InputLabel id="position-select-label">Posição</InputLabel>
                  <Select
                    labelId="position-select-label"
                    id="position-select"
                    value={currentPosition}
                    onChange={onPositionChange}
                  >
                    {IMAGE_POSITIONS.map((position) => (
                      <MenuItem key={position.value} value={position.value}>
                        {position.name}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
              <Grid item xs={12} sm={6} md={2} lg={2}>
                <CategoriesSelect
                  category={currentCategory}
                  onChange={onCategoryChange}
                />
              </Grid>
              <Grid item lg={2}>
                <Button
                  onClick={onApplyClick}
                  color="primary"
                  variant="contained"
                >
                  Aplicar
                </Button>
              </Grid>
            </Grid>
          )}

          <Grid container className="mt-4" spacing={6}>
            {imagesAppearing.map((props) => {
              const { id, name, url, position, category, checked } = props;
              return (
                <Grid key={id} item lg={3} md={3} sm={3}>
                  <Thumb
                    id={id}
                    name={name}
                    url={url}
                    position={position}
                    category={category}
                    checked={checked}
                    handleCheckChange={handleCheckChange}
                    onRemove={removeImage}
                  />
                </Grid>
              );
            })}
          </Grid>

          <ImageUploader onDrop={onDrop} />
          <div className="d-flex flex-row justify-content-end mt-4">
            <Link
              variant="contained"
              color="secondary"
              component={Button}
              to="/packages"
            >
              Cancelar
            </Link>
            <Button
              className="ml-4"
              type="submit"
              variant="contained"
              color="primary"
            >
              Salvar
            </Button>
          </div>
        </Form>
      </Formik>
    </Paper>
  );
};

export default PackageFormDialog;
