/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useContext } from 'react';
import Typography from '@material-ui/core/Typography';
import AuthenticationContext from 'modules/shared/authentication/AuthenticationContext';
import { useHistory, Link } from 'react-router-dom';
import useNotification from 'modules/shared/hooks/useNotification';
import logo from './logo.png'
const Header = () => {
  const notification = useNotification();
  const history = useHistory();
  const { logout } = useContext(AuthenticationContext);
  const onLogoutClick = () => {
    logout();
    history.push('/');
    notification.success('Logout feito com sucesso!');
  };
  return (
    <div className="d-flex justify-content-between align-items-center header p-3"
      style={{
        color: '#fff',
        backgroundColor: 'rgba(0,0,0, 1)'
      }}
    >
      <div className="d-flex align-items-center">
        <img src={logo} />
        <Typography className="ml-2" variant="h5">
          Gerenciador de Pacotes
        </Typography>
      </div>
      <nav>
        <Link className="mr-4" to="/purchases">
          Vendas
        </Link>
        <Link className="mr-4" to="/packages">
          Pacotes
        </Link>
        <Link className="mr-4" to="/categories">
          Categorias
        </Link>
        <Link to="" onClick={onLogoutClick}>
          Logout
        </Link>
      </nav>
    </div>
  );
};

export default Header;
