import { useEffect, useState } from 'react';
import { getPurchases } from 'modules/purchases/services/queries';

export const PROCESS = {
  LOADING: 'loading',
  DONE: 'done',
  ERROR: 'error',
};

export default function usePurchasesPackages() {
  const [purchases, setPurchases] = useState([]);
  const [process, setProcess] = useState(PROCESS.LOADING);

  const fetchPackages = async () => {
    try {
      setProcess(PROCESS.LOADING);
      const result = await getPurchases();
      console.log(result.data.data, 'daqui')

      setPurchases(result.data.data);
      setProcess(PROCESS.DONE);
    } catch (error) {
      setProcess(PROCESS.ERROR);
    }
  };

  useEffect(() => {
    fetchPackages();
  }, []);

  const refetch = () => fetchPackages();

  return { process, purchases, refetch };
}
