import React, { useState } from 'react';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import MuiCard from '@material-ui/core/Card';
import { Button, Typography } from '@material-ui/core';
import Loading from 'modules/shared/components/Loading';
import Card from 'modules/shared/components/Card';
import { usePaginatedQuery } from 'react-query';
import Pagination from '@material-ui/lab/Pagination';
import { GET_CATEGORIES } from 'modules/shared/utils/consts';
import ErrorInfo from 'modules/shared/components/ErrorInfo';
import useEditCategory from '../hooks/useEditCategory';
import useAddCategory from '../hooks/useAddCategory';
import { getCategories } from '../services/categories';
import useRemoveCategory from '../hooks/useRemoveCategory';

const CategoriesList = () => {
  const [page, setPage] = useState(1);
  const backToFirstPage = () => setPage(1);
  const fetch = () => getCategories({ page });
  const { isLoading, error, resolvedData: result } = usePaginatedQuery(
    [GET_CATEGORIES, page],
    fetch,
  );

  const { dialog: editDialog, askForEditCategory } = useEditCategory({
    onSuccess: backToFirstPage,
  });
  const { dialog: removeDialog, askForRemoveCategory } = useRemoveCategory({
    onSuccess: backToFirstPage,
  });
  const { dialog: addDialog, askForAddCategory } = useAddCategory({
    onSuccess: backToFirstPage,
  });

  if (isLoading) {
    return <Loading />;
  }

  if (error) {
    return <ErrorInfo errors={[error]} />;
  }

  const handleChange = (event, value) => setPage(value);

  return (
    <Container>
      <Typography variant="h6" className="mt-4">
        Categorias disponíveis
      </Typography>
      <Grid className="mt-1 mb-4" container spacing={6}>
        {result.data.data.map(({ id, name, description }) => {
          const onEdit = () => askForEditCategory({ id, name, description });
          const onRemove = () => askForRemoveCategory(id);
          return (
            <Grid key={id} item xs={12} sm={6} md={4} lg={4}>
              <Card
                name={name}
                description={description}
                onEdit={onEdit}
                onRemove={onRemove}
              />
            </Grid>
          );
        })}
        <Grid item xs={12} sm={6} md={4} lg={4}>
          <MuiCard className="h-100 package-card">
            <Button
              className="w-100 h-100 font-weight-bold"
              onClick={askForAddCategory}
            >
              +
            </Button>
          </MuiCard>
        </Grid>
      </Grid>
      <Pagination
        className="mt-4 w-100 d-flex justify-content-center"
        count={result.data.pagination.totalPages}
        page={page}
        color="primary"
        onChange={handleChange}
      />
      {addDialog}
      {editDialog}
      {removeDialog}
    </Container>
  );
};

export default CategoriesList;
