import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import PostPurchaseForm from '../views/PostPurchaseForm';

const PostPurchaseRoutes = () => {
  return (
    <Switch>
      <Route exact path="/post-purchase" component={PostPurchaseForm} />
      <Redirect from="/" to="/post-purchase" />
    </Switch>
  );
};
export default PostPurchaseRoutes;
