import React, { useState } from 'react';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import MuiCard from '@material-ui/core/Card';
import Card from 'modules/shared/components/Card';
import { Link, useHistory } from 'react-router-dom';
import { Typography } from '@material-ui/core';
import Loading from 'modules/shared/components/Loading';
import { usePaginatedQuery } from 'react-query';
import Pagination from '@material-ui/lab/Pagination';
import { GET_PACKAGES } from 'modules/shared/utils/consts';
import ErrorInfo from 'modules/shared/components/ErrorInfo';
import useRemovePackage from '../hooks/useRemovePackage';
import usePublishPackage from '../hooks/usePublishPackage';
import { getPackages } from '../services/queries';

const PackagesList = () => {
  const [page, setPage] = useState(1);
  const backToFirstPage = () => setPage(1);
  const fetch = () => getPackages({ page });
  const { isLoading, error, resolvedData: result } = usePaginatedQuery(
    [GET_PACKAGES, page],
    fetch,
  );
  const { askForRemovePackage, dialog: removeDialog } = useRemovePackage({
    onSuccess: backToFirstPage,
  });
  const { askForPublishPackage, dialog: publishDialog } = usePublishPackage({
    onSuccess: backToFirstPage,
  });
  const history = useHistory();
  if (isLoading) {
    return <Loading />;
  }

  if (error) {
    return <ErrorInfo errors={[error]} />;
  }

  const handleChange = (event, value) => setPage(value);

  return (
    <Container>
      <Typography variant="h6" className="mt-4">
        Pacotes disponíveis
      </Typography>
      <Grid className="mt-1" container spacing={6}>
        {result.data.data.map(
          ({ id, name, description, is_published: isPublished }) => {
            const onEdit = () => history.push(`/packages/${id}`);
            const onRemove = () => askForRemovePackage(id);
            const onPublish = () => askForPublishPackage(id);
            return (
              <Grid key={id} item xs={12} sm={6} md={4} lg={4}>
                <Card
                  name={name}
                  description={description}
                  isPublished={isPublished}
                  onEdit={onEdit}
                  onRemove={onRemove}
                  onPublish={onPublish}
                />
              </Grid>
            );
          },
        )}
        <Grid item xs={12} sm={6} md={4} lg={4}>
          <MuiCard className="h-100 package-card">
            <Link
              component={Button}
              className="w-100 h-100 font-weight-bold"
              to="/packages/new"
            >
              +
            </Link>
          </MuiCard>
        </Grid>
      </Grid>
      <Pagination
        className="mt-4 w-100 d-flex justify-content-center"
        count={result.data.pagination.totalPages}
        page={page}
        color="primary"
        onChange={handleChange}
      />
      {removeDialog}
      {publishDialog}
    </Container>
  );
};

export default PackagesList;
