import React, { useState } from 'react';
import AlertDialog from 'modules/shared/components/AlertDialog';
import useNotification from 'modules/shared/hooks/useNotification';
import { useHistory } from 'react-router-dom';
import { Typography } from '@material-ui/core';
import { getContentType } from 'modules/shared/utils/consts';
import { calculateImages } from 'modules/shared/utils/img';
import { getSignedURLS } from '../services/queries';
import { uploadImage } from '../services/update';
import { savePosts } from '../services/update';
import ProgressDialog from '../components/ProgressDialog';

const usePackageAdd = () => {
  const [dialog, setDialog] = useState(null);
  const notification = useNotification();
  const history = useHistory();
  const onClose = () => setDialog(null);

  const buildPosts = async ({ images }) => {
    let signedData;
    try {
      const contentTypes = images.map((image) => ({
        'Content-Type': getContentType(image),
      }));
      signedData = await getSignedURLS(contentTypes);
    } catch (error) {
      console.error(error);
      onClose();
    }

    const posts = [];
    const uploadErrors = [];
    for (let i = 0; i < images.length; i += 1) {
      const signed = signedData.data.posts[i];
      const isVideo = /.mp4/.test(images[i].name)

      const { coordinates } = !isVideo && await calculateImages(images[i]) || {};
      try {
        setDialog(
          <ProgressDialog
            progress={i}
            total={images.length}
            onClose={onClose}
          />,
        );
        await uploadImage({ url: signed.uploadURL, img: images[i].file });
        posts.push({
          id: signed.id,
          package_id: signedData.data.id,
          category_id: images[i].category.id,
          name: images[i].file.name,
          aws_path: signed.aws_path,
          is_customizable: !isVideo,
          watermark_position: images[i].position.value,
          content_type: getContentType(images[i].file),
          coordinate_x: coordinates && coordinates.coordinate_x || '0',
          coordinate_y: coordinates && coordinates.coordinate_y || '0',
        });
      } catch (error) {
        console.log(error);
        uploadErrors.push(images[i]);
      }
    }

    return { posts, uploadErrors, packageId: signedData.data.id };
  };

  async function savePackage({ name, description, images }) {
    const { packageId, posts, uploadErrors } = await buildPosts({ images });
    if (posts.length < 1) {
      return notification.error('Ocorreu um erro ao salvar as imagens!');
    }
    try {
      await savePosts({
        id: packageId,
        name,
        description,
        posts,
      });
    } catch (error) {
      onClose();
      return notification.error('Ocorreu um erro ao finalizar a criação!');
    }

    const onConfirm = () => {
      onClose();
      history.push('/packages');
    };

    return setDialog(
      <AlertDialog title="Processamento concluído" open onConfirm={onConfirm}>
        {uploadErrors.length < 1 ? (
          <Typography>Suas imagens foram salvas com sucesso!</Typography>
        ) : (
          <div>
            <Typography variant="body1">Seu pacote foi criado!</Typography>
            <Typography className="font-weight-bold mt-2" variant="body1">
              Infelizmente, ocorreu um problema com as seguintes imagens:
            </Typography>
            {uploadErrors.map((image) => (
              <Typography key={image.id} className="mt-1" variant="body2">
                {image.file.name}
              </Typography>
            ))}
          </div>
        )}
      </AlertDialog>,
    );
  }

  const askForSave = async ({ name, description, images }) => {
    const onConfirm = () => savePackage({ name, description, images });
    setDialog(
      <AlertDialog
        title="Adicionar pacote"
        question="Você confirma as imagens adicionadas?"
        open
        onClose={onClose}
        onConfirm={onConfirm}
      />,
    );
  };

  return {
    askForSave,
    dialog,
  };
};

export default usePackageAdd;
