import React from 'react';
import { QueryCache, ReactQueryCacheProvider } from 'react-query';
import { STALE_TIME } from 'modules/shared/utils/consts';
import RootRoutes from './config/RootRouters';
import ThemeProvider from './modules/shared/theme/ThemeProvider';
import NotificationProvider from './modules/shared/providers/NotificationProvider';
import AuthenticationProvider from './modules/shared/authentication/AuthenticationProvider';

const queryCache = new QueryCache({
  defaultConfig: { queries: { staleTime: STALE_TIME } },
});

const App = () => (
  <ThemeProvider>
    <AuthenticationProvider>
      <NotificationProvider>
        <ReactQueryCacheProvider queryCache={queryCache}>
          <RootRoutes />
        </ReactQueryCacheProvider>
      </NotificationProvider>
    </AuthenticationProvider>
  </ThemeProvider>
);

export default App;
