import { GET_ALL_CATEGORIES } from 'modules/shared/utils/consts';
import { useQuery } from 'react-query';
import { getCategories } from '../services/categories';

const GREAT_NUMBER = 10000000;

const useGetCategories = ({ onEmptyResult } = { onEmptyResult: () => {} }) => {
  const onSuccess = (res) => {
    if (res.data.data.length < 1) {
      onEmptyResult();
    }
  };

  const {
    isLoading,
    error,
    data: categoriesResult,
  } = useQuery(
    GET_ALL_CATEGORIES,
    () => getCategories({ page: 1, limit: GREAT_NUMBER }),
    { onSuccess },
  );

  if (isLoading || error) {
    return {
      categoriesById: {},
      categories: [],
      isLoading,
      error,
    };
  }

  const categoriesById = categoriesResult.data.data.reduce(
    (acum, current) => ({
      ...acum,
      [current.id]: current,
    }),
    {},
  );

  return {
    categoriesById,
    categories: categoriesResult.data.data,
    isLoading,
    error,
  };
};

export default useGetCategories;
