/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
import React from 'react';
import { Typography, Checkbox } from '@material-ui/core';

const timesStyle = {
  width: '20px',
  height: '20px',
  position: 'absolute',
  paddingLeft: '5px',
  right: '-45px',
  top: '-7px',
  backgroundColor: 'rgb(199, 199, 199)',
  borderRadius: '10px',
  cursor: 'pointer',
};

const containerStyle = {
  width: 60,
};

export default function Thumb(props) {
  const {
    id,
    url,
    name,
    position,
    category,
    handleCheckChange,
    checked,
    onRemove,
  } = props;
  const onRemoveClick = () => onRemove(id);
  const onChange = () => handleCheckChange(id);
  return (
    <div className="pos-relative">
      <div className="d-flex pos-relative">
        <div className="mt-4">
          <Checkbox checked={checked} onChange={onChange} />
        </div>
        <div className="pos-relative" style={containerStyle}>
          <span onClick={onRemoveClick} style={timesStyle}>
            &times;
          </span>
          <img
            src={url}
            style={{ width: 100, height: 100, border: '2px solid #c7c7c7' }}
            alt="for package"
          />
        </div>
      </div>
      <div className="pos-relative">
        <Typography variant="body2" className="mt-1">
          Nome: <span className="font-weight-bold">{name || '-'}</span>
        </Typography>
        <Typography variant="body2" className="mt-1">
          Posição:{' '}
          <span className="font-weight-bold">
            {position ? position.name : '-'}
          </span>
        </Typography>
        <Typography variant="body2" className="mt-1">
          Categoria:{' '}
          <span className="font-weight-bold">
            {category ? category.name : '-'}
          </span>
        </Typography>
      </div>
    </div>
  );
}
