import API from 'config/api';
import { QUERY_LIMIT } from 'modules/shared/utils/consts';

export const getPackages = ({ page = 1, limit = QUERY_LIMIT }) =>
  API.get(`/packages?page=${page}&limit=${limit}`);

export const getPackageById = async ({ id }) => API.get(`/packages/${id}`);

export const getPackagePostsById = async ({
  id,
  page = 1,
  limit = QUERY_LIMIT,
}) => API.get(`packages/${id}?page=${page}&limit=${limit}`);

export const getSignedURLS = (types) =>
  API.post('/packages/generate-urls', types);
