import React from 'react';
import { useState } from 'react';
import useNotification from 'modules/shared/hooks/useNotification';
import { useMutation, useQueryCache } from 'react-query';
import AlertDialog from 'modules/shared/components/AlertDialog';
import { GET_CATEGORIES } from 'modules/shared/utils/consts';
import { removeCategory } from '../services/categories';

const useRemoveCategory = ({ onSuccess }) => {
  // eslint-disable-next-line no-use-before-define
  const [mutate] = useMutation((newData) => removeCategory(newData));
  const queryCache = useQueryCache();
  const [dialog, setDialog] = useState(null);
  const onClose = () => setDialog(null);
  const notification = useNotification();

  const askForRemoveCategory = (id) => {
    const onConfirm = async () => {
      try {
        await mutate(id);
        notification.success('Categoria removida com sucesso.');
        queryCache.invalidateQueries(GET_CATEGORIES);
        onSuccess();
        onClose();
      } catch (error) {
        notification.error('Ocorreu um erro ao remover a categoria.');
      }
    };
    setDialog(
      <AlertDialog
        title="Remover categoria"
        question="Deseja mesmo remover esta categoria?"
        open
        onConfirm={onConfirm}
        onClose={onClose}
      />,
    );
  };

  return {
    askForRemoveCategory,
    dialog,
  };
};

export default useRemoveCategory;
