import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import PackagesList from '../view/PackagesList';
import NewPackage from '../view/NewPackage';
import EditPackage from '../view/EditPackage';

const PackagesRoutes = () => {
  return (
    <Switch>
      <Route exact path="/packages/new" component={NewPackage} />
      <Route exact path="/packages/:id" component={EditPackage} />
      <Route exact path="/packages" component={PackagesList} />
      <Redirect from="/" to="/packages" />
    </Switch>
  );
};
export default PackagesRoutes;
