import React from 'react';
import './index.scss';
import Typography from '@material-ui/core/Typography';
import MuiCard from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';

export default function Card({
  name,
  description,
  isPublished,
  onEdit,
  onRemove,
  onPublish,
}) {
  return (
    <MuiCard className="h-100 package-card">
      <CardContent className="h-100 d-flex flex-column justify-content-between">
        <div>
          <Typography variant="h6">{name}</Typography>
          <Typography className="mt-2" variant="body1">
            {description}
          </Typography>
        </div>
        {onPublish && isPublished && (
          <Typography variant="body2" className="font-italic mt-3">
            Publicado
          </Typography>
        )}
        <Grid container spacing={1} className="mt-3">
          {onPublish && !isPublished && (
            <Grid item xs={12} sm={4} md={4} lg={4}>
              <Button
                onClick={onPublish}
                className="w-100"
                variant="contained"
                color="primary"
              >
                Publicar
              </Button>
            </Grid>
          )}
          <Grid item xs={12} sm={4} md={4} lg={4}>
            <Button
              onClick={onEdit}
              className="w-100"
              variant="contained"
              color="secondary"
            >
              Editar
            </Button>
          </Grid>
          <Grid item xs={12} sm={4} md={4} lg={4}>
            <Button
              onClick={onRemove}
              className="red-button w-100"
              variant="contained"
            >
              Remover
            </Button>
          </Grid>
        </Grid>
      </CardContent>
    </MuiCard>
  );
}
