import API from 'config/api';
import { QUERY_LIMIT } from 'modules/shared/utils/consts';

export const getCategories = async ({ page = 1, limit = QUERY_LIMIT }) =>
  API.get(`/categories?page=${page}&limit=${limit}`);

export const newCategory = (data) => API.post('/categories', data);

export const removeCategory = (id) => API.delete(`/categories/${id}`);

export const updateCategory = ({ id, name, description }) =>
  API.put(`/categories/${id}`, { name, description });
