import React from 'react';
import { Dialog, Typography, Card, CardContent } from '@material-ui/core';
import CategoryForm from './CategoryForm';

export default function CategoryEditDialog({ onClose, open, data, onSubmit }) {
  return (
    <Dialog open={open} onClose={onClose}>
      <Card>
        <CardContent className="p-4">
          <Typography variant="h6" className="mb-4">
            Editar Categoria
          </Typography>
          <CategoryForm
            initialValues={data}
            onSubmit={onSubmit}
            onCancel={onClose}
          />
        </CardContent>
      </Card>
    </Dialog>
  );
}
