const watermarkWidth = {
  x: 200,
  y: 200,
};
function calculateCoordinates(x, y, position) {
  switch (position) {
    case 0:
      return {
        coordinate_x: String(Math.floor(x * 0.02)),
        coordinate_y: String(Math.floor(y * 0.02)),
      };
    case 1:
      return {
        coordinate_x: String(Math.floor(x * 0.98) - watermarkWidth.x),
        coordinate_y: String(Math.floor(y * 0.02)),
      };
    case 2:
      return {
        coordinate_x: String(Math.floor(x * 0.5) - watermarkWidth.x / 2),
        coordinate_y: String(Math.floor(y * 0.5) - watermarkWidth.y / 2),
      };
    case 3:
      return {
        coordinate_x: String(Math.floor(x * 0.02)),
        coordinate_y: String(Math.floor(y * 0.98) - watermarkWidth.y),
      };
    case 4:
      return {
        coordinate_x: String(Math.floor(x * 0.98) - watermarkWidth.x),
        coordinate_y: String(Math.floor(y * 0.98) - watermarkWidth.y),
      };
    default:
      return {
        coordinate_x: String(0),
        coordinate_y: String(0),
      };
  }
}

function getImageDimensions(file) {
  return new Promise(function getImagePromise(resolved, rejected) {
    const i = new Image();
    i.onload = function onLoadImg() {
      resolved({ w: i.width, h: i.height });
    };
    i.onerror = function onErrorImg(error) {
      rejected(error);
    };
    i.src = file;
  });
}

async function calculateImages(image) {
  const dimmensions = await getImageDimensions(image.url);
  image.dimmensions = dimmensions;
  image.coordinates = calculateCoordinates(
    dimmensions.w,
    dimmensions.h,
    image.position.value,
  );
  return {
    coordinates: image.coordinates,
  };
}

export { calculateCoordinates, getImageDimensions, calculateImages };
