import Jimp from 'jimp/es';

function getToRender (formValues) {
    console.log('###### renderOneLine', 'lato-italic-24');
    return renderOneLine(formValues, 'lato-italic-24', '.image-container img')

}
const roleMap = {
  0: 'CONSULTORA',
  1: 'DIRETORA',
};

function renderOneLine({ customName, role, instagram, phone, contact, customLastName, fontSize = 20}) {
  const contact2 = contact === '0' ? instagram : phone;
  return Jimp.read('https://gmreis.github.io/watermark-browser/dist/img/unnamed.png')
    // Jimp.read('/img/untitled.png')
      .then(async unnamed => {
        const nameFont = await Jimp.loadFont(`https://gmreis.github.io/watermark-browser/dist/fonts/lato-italic/lato-italic-bold-${fontSize}.fnt`)
        const rankFont = await Jimp.loadFont('https://gmreis.github.io/watermark-browser/dist/fonts/lato-italic/lato-italic-12.fnt')

        // console.log('measureText name', Jimp.measureText(nameFont, name))
        // console.log('measureTextHeight name', Jimp.measureTextHeight(nameFont, name))

        const rankConfig = {
            text: roleMap[role],
            alignmentX: Jimp.HORIZONTAL_ALIGN_CENTER,
            alignmentY: Jimp.VERTICAL_ALIGN_MIDDLE
        };

        const companyConfig = {
            text: 'MARY KAY',
            alignmentX: Jimp.HORIZONTAL_ALIGN_CENTER,
            alignmentY: Jimp.VERTICAL_ALIGN_MIDDLE
        }

        const contactConfig = {
            text: contact2,
            alignmentX: Jimp.HORIZONTAL_ALIGN_CENTER,
            alignmentY: Jimp.VERTICAL_ALIGN_MIDDLE
        }

        const firstLineConfig = {
          text: customName,
          alignmentX: Jimp.HORIZONTAL_ALIGN_CENTER,
          alignmentY: Jimp.VERTICAL_ALIGN_BOTTOM
        };

        const lastLineConfig = {
          text: customLastName,
          alignmentX: Jimp.HORIZONTAL_ALIGN_CENTER,
          alignmentY: Jimp.VERTICAL_ALIGN_BOTTOM
        };
        const rankImage = await unnamed.print(rankFont, 0, 30, rankConfig, 180, 0);
        const companyImage = await rankImage.print(rankFont, 0, 40, companyConfig, 180, 0);
        const contactImage = await companyImage.print(rankFont, 0, 138, contactConfig, 180, 0);

        const lastLineHeight = Jimp.measureTextHeight(nameFont, customLastName)
        const firstLineimage = await contactImage.print(nameFont, 20, 95, firstLineConfig, 140, 0);
        const imageFull = await firstLineimage.print(nameFont, 20, 90, lastLineConfig, 140, (lastLineHeight * 0.98));

        const base64 = await imageFull.getBase64Async('image/png');
        return base64;
    })
    .catch(err => {
        console.error(err);
    });
}


export default getToRender;
